@import _fonts
@import _vars
@import _defaults
body
	background-color: $bodyBg
	// min-height: 200vh
	// @media screen and ( max-width: 1024px )
		// padding-top: #{90px + 30px}
i
	display: inline-block
	width: 16px
	height: 16px
	cursor: pointer
img
	width: 100%
	height: 100%
	object-fit: contain
.logo
	width: 98px
	height: 50px
.btn, .btnreverse
	cursor: pointer
	font-family: firagobold
	font-weight: 700
	@include font(14px, 19.6px)
	padding: 16px 32px
	color: $dark
	@include center()
	max-width: max-content
	// min-width: 100%
	border: 1px solid $dark
	@include transition()
	@include uppercase()
	
	@media screen and ( max-width: 700px )
		min-width: 100%
	&:hover
		background-color: $dark
		color: #fff
.btnreverse
	color: #fff 
	border-color: $dark 
	background-color: $dark 
	&:hover
		background-color: #fff 
		color: $dark 


body.adminbody
	padding: 20px
	display: grid
	grid-template-columns: 1fr 1fr
	height: 100vh
	align-items: stretch
	font-family: firagosemibold
	.cover
		display: flex
		width: 100%
		height: 100%
		background: url(../img/admincover.png) no-repeat
		background-size: cover
		// background-position: 100%
	.login
		position: relative
		@include center()
		padding: 0 20px
		
		&__header
			display: flex
			align-items: center
			position: absolute
			top: 0
			right: 0
			width: calc(100% - 20px)
			justify-content: space-between
			.redirect
				font-family: firagobold
				color: $dark
				@include uppercase()
				@include font(14px, 17.09px)
		&__form
			max-width: 453px
			width: 100%
			display: flex
			flex-direction: column
			input
				border: 1px solid $dark
				padding: 0 20px
				display: flex
				align-items: center
				height: 52px
				font-family: firagobook
				color: $dark
				@include font(14px, 30px)
				
				margin-bottom: 30px
				&:first-child
					margin-bottom: 10px
			button
				width: 100%
				max-width: unset